.App {
  text-align: left;
  overflow-x: hidden;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #833b37;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
}

.Top-bar {
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
  gap: 8vw;
  padding-left: 6vw;
  width: 94vw;
  height: 10vh;
  background-color: #3d0c0a;
}

.Nav-button{
  width: auto;
  height: auto;
  font-size: calc(14px + 1vmax);
}